<template>
  <div id="detail" v-loading="loading">

    <el-row>
      <el-col :span="24">
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ path: '/index' }">首页</el-breadcrumb-item>
          <el-breadcrumb-item>书评详情</el-breadcrumb-item>
          <el-breadcrumb-item v-if="bookInfo">  {{bookInfo.title}}</el-breadcrumb-item>
        </el-breadcrumb>
      </el-col>
    </el-row>
    <br>

    <el-row v-if="detail">
      <!--书本信息-->
      <el-col :offset="2" :span="20">
        <el-row style="background-color: gainsboro">
          <el-col :span="24" style="padding: 5px">


            <img :src="detail.cover" class="image" style="max-height: 210px;max-width: 210px;float: left;padding: 10px">

            <h3 style="text-align: left">
              {{detail.name}}
            </h3>

            <span class="post_meta_top">
              {{detail.bookAuthor}}
            </span>

            <p class="post_meta_top">豆瓣来源:&nbsp;<a :href="detail.url" target="_blank">{{detail.url}}</a></p>

            <p class="post_content">

              {{detail.desc}}
            </p>
          </el-col>
        </el-row>
      </el-col>

      <el-col :offset="2" :span="20">
        <br>
      </el-col>

      <!--书评-->
      <el-col :offset="2" :span="20" style="background-color: #e9e9e2;padding: 10px">
        <h3>
          {{detail.readTitle}}
        </h3>
        <div style="height: 35px;line-height: 35px">
          <div style="float: left;line-height: 35px">
            <vue-letter-avatar :name='detail.authorInfo.name' size='30' :rounded='true' />&nbsp;
          </div>
          <span style="float: left"  class="post_meta_top">{{detail.authorInfo.name}} - {{detail.ctDes}}</span>
          <span style="float: left"  class="post_meta_top">&nbsp; 总打赏: {{detail.valueNas}} Nas</span>
        </div>
        <p class="post_content">{{detail.comment}}</p>
      </el-col>

      <!--其他人评论-->
      <el-col :offset="2" :span="20">
        <h3>评论</h3>
      </el-col>
      <el-col :offset="2" :span="20" v-for="(item, k) in detail.payItems" :key="k" style="padding-top:10px;padding-bottom:10px;border-top:1px solid #E4E7ED">
        <div style="height: 35px;line-height: 35px">
          <div style="float: left;line-height: 35px">
            <vue-letter-avatar :name='item.fromName' size='30' :rounded='true' />&nbsp;
          </div>
          <span style="float: left"  class="post_meta_top">
            {{item.fromName}} - {{item.ctDes}}
            <span v-if="item.value != 0">
              &nbsp;打赏: {{item.valueNas}} Nas
            </span>
          </span>
        </div>
        <p class="post_content">{{item.comment}}</p>
      </el-col>

      <!--我发布评论-->
      <el-col :offset="2" :span="20">
        <el-form :model="subform" ref="subform" :rules="subformRules">
          <el-form-item label="我的评论" prop="comment">
            <el-input type="textarea" minlength="1" maxlength="150"
                      v-model="subform.comment" auto-complete="off"
                      :autosize="{ minRows: 6, maxRows: 6}"></el-input>
          </el-form-item>
          <el-form-item label="打赏金额">
            <el-input type="number" v-model="subform.reward" auto-complete="off">
              <template slot="append">Nas</template>
            </el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="subMyComment(detail.rid)">提交评论</el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import {mapState} from 'vuex'
import _ from 'lodash'
import nebulas from 'nebulas'

export default {
  data () {
    return {
      loading: false,
      subformTile: '',
      queryId: null,
      subform: {
        comment: '',
        reward: 0
      },
      subformRules: {
        comment: [
          {required: true, message: '请输入评论', trigger: 'blur'},
          {min: 1, max: 50, message: '长度在 1 到 150 个字符', trigger: 'blur'}
        ],
        reward: []
      },
      detail: null
    }
  },
  methods: {
    getDetail: function (id) {
      let vueThis = this;
      vueThis.bookInfo = {}
      vueThis.loading = true;
      console.log('getDetail', id)
      vueThis.$store.dispatch('simulateCallNew', {funcName: 'getReadById', params: [id, 'Y', 'Y']}).then((ret) => {
        let data = JSON.parse(ret);
        vueThis.detail = data;
        console.log('获取书评详情', data);
        let d = new Date(vueThis.detail.ct)
        vueThis.detail.ctDes = d.toLocaleDateString() + ' ' + d.toLocaleTimeString();
        vueThis.detail.valueNas = nebulas.Unit.fromBasic(vueThis.detail.value, 'nas');
        vueThis.detail.valueNas = _.replace(vueThis.detail.valueNas, '"', '');

        _.forEach(vueThis.detail.payItems, function (item) {
          let d = new Date(item.ct)
          item.ctDes = d.toLocaleDateString() + ' ' + d.toLocaleTimeString();
          item.valueNas = nebulas.Unit.fromBasic(item.value, 'nas');
          item.valueNas = _.replace(item.valueNas, '"', '');
        });
        vueThis.loading = false;
        vueThis.changePage(1);
      }, (error) => {
        vueThis.loading = false;
        vueThis.showErr('获取书评详情失败' + error);
      });
    },
    // 评论
    subMyComment: function (rid) {
      let vueThis = this
      if (!vueThis.user) {
        vueThis.showErr('请先登录注册！');
        return false;
      }
      vueThis.$refs['subform'].validate((valid) => {
        if (valid) {
          let f = vueThis.subform;
          let params = [vueThis.user.name + '', rid + '', f.comment + ''];
          vueThis.loading = true;
          vueThis.$store.dispatch('callNew', {money: f.reward, funcName: 'sendPay', params: params, goods: {name: '评论'}}).then((ret) => {
            vueThis.loading = false;
            console.log('评论成功', ret);
            vueThis.showSuccess('评论成功, 3秒后自动刷新页面');
            _.delay(function () {
              vueThis.subform.comment = '';
              vueThis.subform.reward = 0;
              vueThis.getDetail(vueThis.queryId);
            }, 3000)
          }, (error) => {
            console.log('评论失败', error);
            vueThis.loading = false;
            vueThis.showErr('评论失败' + error);
          });
        } else {
          vueThis.showErr('请填写评论内容！');
        }
      })
    }
  },
  created () {
    this.queryId = this.$route.query.id;
    this.getDetail(this.queryId)
  },
  computed: mapState([
    'user'
  ])
}
</script>

<style>
  #detail {
  }

  #detail .post_content {
    padding: 10px;
    font-family: 'Poppins', sans-serif;
    font-size: 14px;
    line-height: 1.75;
    color: #757575;
    font-weight: 400;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }

  #detail .post_meta_top{
    color: #666;
    font-size: 14px;
    font-style: italic;
  }
</style>



