<template>
  <div id="index">

    <el-carousel type="card" height="300px">
      <el-carousel-item v-for="(item,key) in carouselItems" :key="key">
        <img :src="'../static/' + item.pic"/>
      </el-carousel-item>
    </el-carousel>

    <el-row v-loading="loading" v-for="(item, k) in showList" :key="k">
      <el-col :span="24">
        <h3 :class="item.class">～～～～ {{item.name}} ～～～～</h3>
      </el-col>

      <el-col :span="6" v-for="(o, index) in item.data" :key="index" style="padding: 5px">
        <el-card :body-style="{ padding: '10px'}" @click.native="showDetail(o.rid)">
          <div style="width: 100%;text-align: center">
            <img :src="o.cover" class="image" style="max-width: 300px;max-height: 300px;">
          </div>
          <div style="padding: 5px;">
            <p class="post_title">{{ o.readTitle }}</p>
            <p class="post_meta_top">{{ o.authorInfo.name }} - <time class="time">{{ o.ctDes }}</time></p>
            <p class="post_content">
              总赏金: {{ o.valueNas }} Nas
              &nbsp;
              总评论: {{ o.payNum }}
            </p>
          </div>
        </el-card>
      </el-col>
    </el-row>

    <div style="text-align: right;margin-top: 10px;">
      <el-pagination
        background
        layout="prev, pager, next"
        @current-change="changePage"
        :current-page.sync="pageInfo.current"
        :page-size="pageInfo.size"
        :total="pageInfo.total">
      </el-pagination>
    </div>

  </div>
</template>

<script>
import {mapState} from 'vuex'
import _ from 'lodash'
import nebulas from 'nebulas'

export default {
  data () {
    return {
      loading: false,
      carouselItems: [
        {pic: 'b1.jpg', label: '悦读时间1'},
        {pic: 'b2.jpg', label: '悦读时间2'},
        {pic: 'b3.jpg', label: '悦读时间3'}
      ],
      pageInfo: {current: 1, size: 4},
      listAll: [],
      showList: {
        'hot': {name: '最热', class: 'hot-title', data: []},
        'normal': {name: '最新', class: 'all-title', data: []}
      }
    }
  },
  methods: {
    // 查看书评详情
    showDetail: function (id) {
      console.log('书评详情', id)
      this.$router.push({name: 'detail', query: {id: id}})
    },
    // 获取书评列表
    getList (o) {
      var vueThis = this
      vueThis.loading = true;
      vueThis.$store.dispatch('simulateCallNew', {funcName: 'getAllReadList'}).then((ret) => {
        let data = JSON.parse(ret);
        console.log('获取书评列表', data);
        vueThis.listAll = data;
        // 循环初始化
        _.forEach(vueThis.listAll, function (item) {
          let d = new Date(item.ct)
          item.ctDes = d.toLocaleDateString() + ' ' + d.toLocaleTimeString();
          item.valueNas = nebulas.Unit.fromBasic(item.value, 'nas');
          item.valueNas = _.replace(item.valueNas, '"', '');
        });
        // 最新排序
        vueThis.listAll = _.orderBy(vueThis.listAll, ['ct'], ['desc']);
        // 最热排序
        let hotSort = _.orderBy(vueThis.listAll, ['payNum'], ['desc']);
        vueThis.showList['hot'].data = _.slice(hotSort, 0, 4);

        vueThis.loading = false;
        vueThis.changePage(1);
      }, (error) => {
        vueThis.loading = false;
        vueThis.showErr('获取书评列表失败' + error);
      });
    },
    // [所有]修改页码
    changePage (pageNum) {
      let vueThis = this;
      vueThis.loading = true;
      vueThis.showList['normal'].data = [];
      // 分页处理
      let p = vueThis.pageInfo;
      p.current = !pageNum ? 1 : pageNum;
      let maxPage = Math.ceil(p.total / p.size);
      p.current = _.min([p.current, maxPage])
      p.total = _.get(vueThis.listAll, 'length', 0);

      let start = (p.current - 1) * p.size;
      let end = _.min([p.current * p.size, p.total]);

      vueThis.showList['normal'].data = _.slice(vueThis.listAll, start, end);
      vueThis.loading = false;
    }
  },
  created () {
    this.getList()
  },
  computed: mapState([
    'user'
  ])
}
</script>

<style>
  #index{
  }

  #index .hot-title{
    padding: 10px;
    background-color: rgba(255, 69, 0, 0.68);
    color: white;
    text-align: center;
  }
  #index .all-title{
    padding: 10px;
    background-color: rgba(0, 186, 189, 1);
    color: white;
    text-align: center;
  }
  #index .el-carousel__item h3 {
    color: #475669;
    font-size: 18px;
    opacity: 0.75;
    line-height: 300px;
    margin: 0;
  }

  #index .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }

  #index .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }

  #index .post_title {
    margin: 0px;
    color: #252525;
    font-weight: 500;
    word-wrap: break-word;
  }

  #index .post_meta_top {
    margin: 0px;
    color: #666;
    font-size: 14px;
    font-style: italic;
  }

  #index .post_content {
    margin: 0px;
    font-family: 'Poppins', sans-serif;
    font-size: 14px;
    color: #757575;
    font-weight: 400;
  }
</style>



