<template>
  <div id="mine" v-loading="loading">

    <el-table
      :data="list"
      border
      style="width: 100%">

      <el-table-column
        label="序号"
        width="50"
        prop="index">
      </el-table-column>

      <el-table-column
        label="豆瓣地址"
        width="220">
        <template slot-scope="scope">
          <a :href="scope.row.url" target="_blank">
            {{scope.row.url}}
          </a>
        </template>
      </el-table-column>

      <el-table-column
        label="书名"
        prop="name">
      </el-table-column>

      <el-table-column
        label="书评标题"
        prop="readTitle">
      </el-table-column>

      <el-table-column
        label="获得打赏"
        prop="valueNas">
      </el-table-column>

      <el-table-column
        label="创建时间"
        prop="ctDes">
      </el-table-column>

      <el-table-column
        label="操作"
        width="120">
        <template slot-scope="scope">
          <el-button
            @click.native.prevent="showDetail(scope.row.rid)"
            type="text"
            size="small">
            查看详情
          </el-button>
        </template>
      </el-table-column>

    </el-table>

    <div style="text-align: right;margin-top: 10px;">
      <el-pagination
        background
        layout="prev, pager, next"
        @current-change="changePage"
        :current-page.sync="pageInfo.current"
        :page-size="pageInfo.size"
        :total="pageInfo.total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import {mapState} from 'vuex'
  import _ from 'lodash'
  import nebulas from 'nebulas'

  export default {
    data () {
      return {
        listAll: [],
        list: [],
        authorInfo: null,
        loading: false,
        pageInfo: {current: 1, size: 5}
      }
    },
    methods: {
      // 查看书评详情
      showDetail: function (id) {
        this.$router.push({name: 'detail', query: {id: id}})
      },
      // 获取书评列表
      getList: function () {
        var vueThis = this
        vueThis.loading = true;
        vueThis.$store.dispatch('simulateCallNew', {funcName: 'getUserReadList'}).then((ret) => {
          let data = JSON.parse(ret);
          console.log('获取书评列表', data);
          vueThis.authorInfo = data.authorInfo;
          vueThis.listAll = data.readList;
          // 遍历增加序号
          _.forEach(vueThis.listAll, function (item, index) {
            item.index = index + 1;
            let d = new Date(item.ct)
            item.ctDes = d.toLocaleDateString() + ' ' + d.toLocaleTimeString();
            item.valueNas = nebulas.Unit.fromBasic(item.value, 'nas');
            item.valueNas = _.replace(item.valueNas, '"', '');
          });
          vueThis.loading = false;
          vueThis.changePage(1);
        }, (error) => {
          vueThis.loading = false;
          vueThis.showErr('获取书评列表失败' + error);
        });
      },
      // 修改页码
      changePage (pageNum) {
        console.log('修改页码', pageNum);
        let vueThis = this;
        vueThis.loading = true;
        vueThis.list = [];
        let p = vueThis.pageInfo;
        p.current = !pageNum ? 1 : pageNum;
        let maxPage = Math.ceil(p.total / p.size);
        p.current = _.min([p.current, maxPage])
        p.total = _.get(vueThis.listAll, 'length', 0);

        let start = (p.current - 1) * p.size;
        let end = _.min([p.current * p.size, p.total]);

        vueThis.list = _.slice(vueThis.listAll, start, end);
        vueThis.loading = false;
      }
    },
    created () {
      this.getList()
    },
    computed: mapState([
      'user'
    ])
  }
</script>

<style>
  #mine {
  }

  #mine .demo-table-expand {
    font-size: 0;
  }
  #mine .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }
  #mine .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
</style>



