<template>
  <div id="help">
    <div class="div-1">
     <h3>I 准备工作</h3>
      <ul class="div-2">
        <li>安装Chrome浏览器，并且安装浏览器数字钱包插件；</li>
        <ul class="div-2">
          <p class="div-2">
            步骤1. 【chrome】请打开
            <a href="https://github.com/ChengOrangeJu/WebExtensionWallet">
              https://github.com/ChengOrangeJu/WebExtensionWallet
            </a>
            &nbsp;然后按照流程安装钱包到浏览器；
          </p>
          <p class="div-2">
            步骤2. 设置您的帐户，请妥善包括你到钱包密码和私钥文件；
          </p>
          <p class="div-2">
            步骤3. 代币请到 <a href="https://gate.io">火币网 gate.io</a> 购买兑换；
          </p>
