      <h3> I I  功能说明</h3>
      <li class="div-2">首页：查看最热和最新书评，【注册用户】可以进行评价或者打赏； </li>
      <li class="div-2">用户注册：首次登录钱包功后请注册系统用户； </li>
      <li class="div-2">悦读心得：【注册用户】发布新的书评； </li>
      <li class="div-2">我的悦读：【注册用户】查看我发布过的书评，以及得到的评论和打赏； </li>
      <li class="div-2">关于：系统使用帮助；</li>
    </div>
  </div>
