<script>
export default {
  data () {
    return {
    }
  }
}
</script>

<style scoped>
  #help {
    font-size: 14px;
    color: #606266;
  }
  #help .div-1 {
    text-align: left;
    margin: 20px;
  }
  #help .div-2 {
    text-align: left;
    margin-left: 20px;
    margin-top: 8px;
  }
</style>



