<template>
  <div id="loginUser" v-loading="loading">
    <el-row>
      <el-col :offset="4" :span="16">
        <el-form ref="form" :model="form" label-width="80px" :rules="formRules" label-position="top" class="demo-ruleForm">
          <el-form-item label="昵称" prop="name">
            <el-input v-model="form.name" minlength="1" maxlength="50" placeholder="请输入昵称"></el-input>
          </el-form-item>
          <el-form-item label="自我介绍" prop="addinfo">
            <el-input type="textarea"  minlength="1" maxlength="350"
                      v-model="form.addinfo" :autosize="{ minRows: 2, maxRows: 4}"
                      placeholder="请输入自我介绍"></el-input>
          </el-form-item>
          <el-form-item label="头像颜色" prop="avatar" v-if="false">
            <el-color-picker v-model="form.avatar" placeholder="请选择头像颜色"></el-color-picker>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="signUp">注册</el-button>
            <el-button @click="resetForm()">重置</el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import {mapState} from 'vuex'
import _ from 'lodash'

export default {
  data () {
    return {
      loading: false,
      form: {
        name: '',
        avatar: '#409EFF',
        addinfo: ''
      },
      formRules: {
        name: [
          {required: true, message: '请输入名字', trigger: 'blur'},
          {min: 1, max: 50, message: '长度在 1 到 50 个字符', trigger: 'blur'}
        ],
        avatar: [
          {required: true, message: '请选择头像颜色', trigger: 'blur'}
        ],
        addinfo: [
          {required: true, message: '请输入自我介绍', trigger: 'blur'},
          {min: 1, max: 350, message: '长度在 1 到 350 个字符', trigger: 'blur'}
        ]
      }
    }
  },
  methods: {
    signUp: function () {
      var vueThis = this
      vueThis.$refs['form'].validate((valid) => {
        if (valid) {
          let f = vueThis.form;
          let params = [f.name + '', f.avatar + '', f.addinfo + ''];
          vueThis.loading = true;
          vueThis.$store.dispatch('callNew', {funcName: 'createUser', params: params, goods: {name: '注册'}}).then((ret) => {
            vueThis.loading = false;
            console.log('注册成功', ret);
            vueThis.showSuccess('注册成功, 3秒后自动刷新页面');
            _.delay(function () {
              vueThis.$router.push('/index');
              _.delay(function () {
                location.reload(true);
              }, 300)
            }, 3000)
          }, (error) => {
            console.log('注册失败', error);
            vueThis.loading = false;
            vueThis.showErr('注册失败' + error);
          });
        } else {
          vueThis.showErr('请完善表单必填字段');
        }
      })
    },
    resetForm: function () {
      this.$refs['form'].resetFields();
    }
  },
  computed: mapState([
    'user'
  ])
}
</script>

<style>
  #loginUser {
  }
</style>



