<template>
  <div id="publish">

    <el-steps :active="step" simple>
      <el-step title="1.选择豆瓣图书" icon="el-icon-tickets"></el-step>
      <el-step title="2.录入书评" icon="el-icon-edit"></el-step>
      <el-step title="3.完成" icon="el-icon-success"></el-step>
    </el-steps>
    <br>

    <el-row v-if="bookInfo" v-loading="loading">
      <el-col :offset="2" :span="20">
        <el-collapse accordion v-model="collapseActive">
          <el-collapse-item name="firstC">

            <template slot="title">
              评论书籍信息： {{bookInfo.title}} &nbsp;[点击查看详情]
            </template>

            <el-row style="background-color: gainsboro">
              <el-col :span="24" style="padding: 5px">

                <img :src="bookInfo.cover" class="image" style="max-height: 210px;max-width: 210px;float: left;padding: 10px">

                <h3 style="text-align: left">
                  {{bookInfo.title}}
                </h3>

                <span class="post_meta_top">
                  {{bookInfo.author}}
                </span>

                <p class="post_content">
                  {{bookInfo.desc}}
                </p>
              </el-col>
            </el-row>

          </el-collapse-item>
        </el-collapse>
      </el-col>
    </el-row>


    <el-row v-show="step == 1" v-loading="loading">
      <el-col :offset="2" :span="20">
        <el-form ref="loadBook" :model="loadBook" :rules="loadBookRules" label-position="top">
          <el-form-item label="书籍豆瓣链接, 例如: https://book.douban.com/subject/26871829/" prop="url">
            <el-input minlength="1" maxlength="150" v-model="loadBook.url" placeholder="书籍豆瓣链接"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="loadBookFun">下一步</el-button>
            <el-button @click="resetForm('loadBook')">重置</el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>

    <el-row v-show="step == 2" v-loading="loading">
      <el-col :offset="2" :span="20">
        <el-form ref="form" :model="form" :rules="formRules" label-position="top">
          <el-form-item label="书评标题" prop="readTitle">
            <el-input minlength="1" maxlength="150" v-model="form.readTitle" placeholder="书评标题"></el-input>
          </el-form-item>
          <el-form-item label="书评内容" prop="comment">
            <el-input type="textarea" :autosize="{ minRows: 15, maxRows: 25}"
                      minlength="1" maxlength="1500"
                      v-model="form.comment" placeholder="读书心得">
            </el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitNode">提交</el-button>
            <el-button @click="resetForm('form')">重置</el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>

    <el-row v-show="step == 3" v-loading="loading">
      <el-col :offset="2" :span="20">
        <h1>恭喜，发布书评成功！</h1>
        <el-form ref="form" :model="form" :rules="formRules" label-position="top">
          <el-form-item>
            <el-button type="primary" @click="again">再发布一个</el-button>
            <el-button type="primary" @click="returnHome">返回首页</el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>

  </div>
</template>

<script>
import {mapState} from 'vuex'
import axios from 'axios'

export default {
  data () {
    return {
      collapseActive: 'firstC',
      step: 1,
      loading: false,
      loadBook: {
        url: ''
      },
      loadBookRules: {
        url: [
          {required: true, message: '请输入书籍豆瓣链接', trigger: 'blur'},
          {min: 1, max: 350, message: '长度在 1 到 350 个字符', trigger: 'blur'}
        ]
      },
      bookInfo: null,
      form: {
        readTitle: '',
        comment: ''
      },
      formRules: {
        readTitle: [
          {required: true, message: '请输入书评标题', trigger: 'blur'},
          {min: 1, max: 150, message: '长度在 1 到 150 个字符', trigger: 'blur'}
        ],
        comment: [
          {required: true, message: '请输入读书评内容', trigger: 'blur'},
          {min: 1, max: 1500, message: '长度在 1 到 1500 个字符', trigger: 'blur'}
        ]
      }
    }
  },
  methods: {
    // 再发布一个
    again: function () {
      this.bookInfo = null;
      this.step = 1;
      this.loadBook.url = '';
      this.form.readTitle = '';
      this.form.comment = '';
    },
    // 返回首页
    returnHome: function () {
      this.$router.push({name: 'index'})
    },
    loadBookFun: function () {
      var vueThis = this
      vueThis.$refs['loadBook'].validate((valid) => {
        if (valid) {
          vueThis.loading = true
          axios.post('http://45.76.2.218:8061/', {url: vueThis.loadBook.url}).then(response => {
            if (response.data.title && response.data.cover) {
              vueThis.bookInfo = response.data;
              vueThis.step = 2;
              vueThis.loading = false
            } else {
              vueThis.loading = false
              vueThis.showErr('加载图书信息失败，您可以尝试换个地址！')
            }
          }).catch(function (error) {
            vueThis.loading = false
            vueThis.showErr('加载图书信息异常，请确认地址是否正确！', error)
          })
        } else {
          vueThis.showErr('请完善表单必填字段');
        }
      })
    },
    submitNode: function () {
      var vueThis = this
      vueThis.$refs['form'].validate((valid) => {
        if (valid) {
          var b = vueThis.bookInfo;
          var f = vueThis.form;
          let params = [b.url, b.cover, b.title, b.author, b.desc, f.readTitle, f.comment];
          vueThis.loading = true
          vueThis.$store.dispatch('callNew', {funcName: 'createRead', params: params}).then((ret) => {
            vueThis.loading = false
            vueThis.showSuccess('发布悦读心得成功');
            vueThis.step = 3;
          }, (error) => {
            vueThis.loading = false
            vueThis.showErr('发布悦读心得失败' + error);
          });
        } else {
          vueThis.showErr('请完善表单必填字段');
        }
      })
    },
    resetForm: function (formName) {
      this.$refs[formName].resetFields();
    }
  },
  created () {
  },
  computed: mapState([
    'user'
  ])
}
</script>

<style>
  #publish {
  }

  #publish .post_content {
    font-family: 'Poppins', sans-serif;
    font-size: 14px;
    line-height: 1.75;
    color: #757575;
    font-weight: 400;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }

  #publish .post_meta_top{
    color: #666;
    font-size: 14px;
    font-style: italic;
  }
</style>



