import Vue from 'vue'
import Router from 'vue-router'
import BaseHelp from '@/components/base-help'
import BaseSignUp from '@/components/base-sign-up'
import BaseIndex from '@/components/base-index'
import BasePublish from '@/components/base-publish'
import BaseMine from '@/components/base-mine'
import SubDetail from '@/components/sub-detail'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: BaseIndex
    },
    {
      path: '/index',
      name: 'index',
      component: BaseIndex
    },
    {
      path: '/help',
      name: 'help',
      component: BaseHelp
    },
    {
      path: '/publish',
      name: 'publish',
      component: BasePublish
    },
    {
      path: '/mine',
      name: 'mine',
      component: BaseMine
    },
    {
      path: '/signUp',
      name: 'signUp',
      component: BaseSignUp
    },
    {
      path: '/detail',
      name: 'detail',
      component: SubDetail
    }
  ]
})



